////
/// @group typography
////

/// Mixin for text alignment.
@mixin foundation-text-alignment {
    /*@foundation-text-alignment*/
    @each $size in $breakpoint-classes {
        @include breakpoint($size) {
            @each $align in (left, right, center, justify) {
                @if $size != $-zf-zero-breakpoint {
                    .#{$size}-text-#{$align} {
                        text-align: $align;
                    }
                } @else {
                    .text-#{$align} {
                        text-align: $align;
                    }
                }
            }
        }
    }
}
